* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:root {
	--font-main: 'Poppins', Arial, Helvetica, sans-serif;
	--font-size: clamp(16px, 2.1vw, 21px);
	--color-main: #2c2c2c;
	--color-white: #ffffff;
	--color-orange: #ec9535;
}

html {
	background-color: var(--color-main);
}
body {
	font-family: var(--font-main);
	font-weight: normal;
	font-style: normal;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
}
.container {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	transform-style: preserve-3d;
	.cigarette {
		transform-style: preserve-3d;
		animation: animate 10s linear infinite;
		span {
			position: absolute;
			color: var(--color-white);
			background: rgba(0, 0, 0, 0.25);
			font-weight: 900;
			font-size: var(--font-size);
			line-height: 50%;
			text-transform: uppercase;
			text-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
			transform: translate(-50%, -50%) rotateX(calc(var(--i) * 36deg))
				translateZ(clamp(18px, 2.5vw, 25px));
			transform-style: preserve-3d;
			i {
				font-style: normal;
				color: var(--color-orange);
				&:first-child:after {
					content: 'l';
					color: #ccc;
					text-transform: lowercase;
				}
				&:last-child {
					color: #f00;
					filter: blur(2px);
					text-shadow: -6px 0 3px #000, 8px 0 20px #f00, 8px 0 24px #f00, 9px 0 0 #222,
						12px 0 #555, 16px 0 #666, 20px 0 #888, 24px 0 #999;
				}
			}
		}
	}
	@keyframes animate {
		0% {
			transform: perspective(1000px) rotateX(0deg);
		}
		100% {
			transform: perspective(1000px) rotateX(360deg);
		}
	}
}
